import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'

class EnhancedInput extends Component {
    state = {
        isDirty: false,
    }
    onChange = (e) => {
        this.props.onChange(e.target.value, this.props.name)

        if (this.state.isDirty === true) return
        this.setState({ isDirty: true })
    }

    render() {
        return (
            <input style={{ color: this.state.isDirty ? 'red' : '' }} className={classnames('form-control', {
                'form-control--is-dirty': this.state.isDirty === true,
            })} {...this.props} onChange={this.onChange} />
        )
    }
}

EnhancedInput.propTypes = {

}

export default EnhancedInput



